import React from 'react';
// import parseJson from 'json-parse-better-errors';
import FormRender from 'form-render/lib/antd';
import DefaultSchema from './json/simplest.json';
import CodeBlock from './CodeBlock';
import { Button } from 'antd';
import excelfy from 'form-render-excel';
import { generatorFieldFormat } from './titanStuff/utils';
import FceData from './titanStuff/FceData';

const parseJson = jsonStr => JSON.parse(jsonStr);

const FormRender2 = excelfy(FormRender);

// help functions
const schema2str = obj => JSON.stringify(obj, null, 2) || '';

class Demo extends React.Component {
  state = {
    schemaStr: schema2str(DefaultSchema),
    formData: {},
    error: '',
  };

  componentDidUpdate(prevProps) {
    const { schemaName } = this.props;
    if (prevProps.schemaName !== schemaName) {
      const schema = require(`./json/${schemaName}.json`);
      this.setState({ schemaStr: schema2str(schema) });
    }
  }

  getSchemaString = () => {
    const { schemaName } = this.props;
    const schema = require(`./json/${schemaName}.json`);
    return schema2str(schema);
  };

  tryParse = schemaStr => {
    let schema = {};
    try {
      schema = parseJson(schemaStr);
      if (typeof schema !== 'object') {
        this.setState({ error: 'schema非正确json' });
        return;
      }
      this.setState({ error: '' });
      return schema;
    } catch (error) {
      this.setState({ error: String(error) });
    }
  };

  handleCodeChange = schemaStr => {
    this.setState({ schemaStr });
    this.tryParse(schemaStr);
  };

  handleDataChange = formData => {
    this.setState({ formData });
  };

  handleValidate = valid => {
    console.log('没有通过的校验:', valid);
  };

  transformToJustSchema = schemaStr => {
    const settings = this.tryParse(schemaStr);
    const schema = settings && settings.schema;
    return schema2str(schema);
  };

  handleSubmit = () => {
    const { formData } = this.state;
    alert(JSON.stringify(formData, null, 2));
  };

  render() {
    const { theme, ...formProps } = this.props;
    const { schemaStr, formData } = this.state;
    let schema = {};
    try {
      schema = parseJson(schemaStr);
    } catch (error) {
      console.log(error);
    }
    console.log(generatorFieldFormat(schema));
    return (
      <div className='flex-auto flex'>
        <div className='w-50 h-100 pl2 flex flex-column overflow-auto'>
          <CodeBlock value={schemaStr} onChange={this.handleCodeChange} />
        </div>
        <div className='w-50 h-100 flex flex-column pa2'>
          <div
            className='h-100 overflow-auto pa3 pt4 flex-auto'
            style={{ borderLeft: '1px solid #ddd' }}
          >
            {this.state.error ? (
              <div>{this.state.error}</div>
            ) : (
              <div className='h-100 overflow-auto'>
                <FormRender2
                  {...formProps}
                  schema={generatorFieldFormat(schema)}
                  formData={formData}
                  onChange={this.handleDataChange}
                  onValidate={this.handleValidate}
                  widgets={{ fceData: FceData }}
                />
                <div className='w-100 flex justify-end'>
                  <Button
                    style={{ width: 120 }}
                    type='primary'
                    onClick={this.handleSubmit}
                  >
                    提交
                  </Button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default Demo;
