import { get } from 'lodash';
import { message } from 'antd';

export function generatorFieldFormat(d) {
  for (const k in d) {
    const dk = get(d, `${k}`, {});
    if (d.type == 'object') {
      Object.keys(d.properties).forEach(key => {
        return generatorFieldFormat(d.properties[key]);
      });
    } else if (d['type'] == 'array') {
      if (d['x-config']) {
        d['ui:widget'] = 'fceData';
        return;
      }
      return generatorFieldFormat(d['items']);
    } else if (dk['type'] == 'array') {
      return generatorFieldFormat(dk['items']);
    } else {
      switch (d['x-format']) {
        case '@image()':
          d.format = 'image';
          let tempDescription = get(d, 'description', '');
          if (tempDescription.indexOf('?getAvatar=1') === -1) {
            d.description = `${tempDescription} （ 谨慎使用动图。如要使用动图请优先使用 apng（而非 gif），同时请在 TPS 生成的链接后加上 ?getAvatar=1 ）`;
          }
          d.pattern =
            '^(https:|http:)?(//){1}(img.alicdn.com|gw.alicdn.com)[a-zA-Z0-9-_.!/]+(.jpg|.png|.gif){1}(\\?getAvatar=1)?$';
          break;
        case '@video()':
          d.pattern = '^https://cloud.video.taobao.*.mp4$';
          break;
        case '@datetime()':
          d.format = 'dateTime';
          break;
        case '@time()':
          d.format = 'time';
          break;
        case '@date()':
          d.format = 'date';
          break;
        case '@color()':
          d.format = 'color';
          break;
        case '@url()':
          // d.pattern = '^(https:|http:)?//[a-zA-Z0-9-_./?=:&]+$'
          d.format = 'url';
          break;
        default:
          break;
      }

      switch (d['type']) {
        case 'number':
          if (d['default'] && typeof d['default'] !== 'number') {
            if (isNaN(Number(d['default']))) {
              message.error(
                `${d['title']} 字段默认值格式不对，请反馈给技术小哥 修改模块字段默认值`
              );
              d['default'] = 0;
            } else {
              d['default'] = Number(d['default']);
            }
          }
          break;
        case 'boolean':
          if (d['default'] && typeof d['default'] !== 'boolean') {
            d['default'] = false;
            if (d['default'] === 'true' || d['default'] == 1) {
              d['default'] = true;
            }
          }
          break;
        default:
          break;
      }
    }
  }
  return d;
}
