import React, { Component } from 'react';
import Demo from './main';
import { Radio, Select, Switch, Slider } from 'antd';
import './index.css';
import 'tachyons';
import 'antd/dist/antd.css';

window.copyMe = (list, index) => {
  const item = list[index];
  list.splice(index, 0, item);
  return list;
};

const Option = Select.Option;

class Root extends Component {
  state = {
    schemaName: 'simplest',
    theme: 'antd',
    column: 1,
    displayType: 'column',
    showDescIcon: false,
    readOnly: false,
    labelWidth: 110,
  };

  onThemeChange = e => {
    this.setState({ theme: e.target.value });
  };

  onColumnNumberChange = value => {
    this.setState({ column: value });
  };

  onDisplayChange = value => {
    this.setState({
      displayType: value,
      showDescIcon: value === 'row',
    });
  };

  onShowDescChange = value => {
    this.setState({ showDescIcon: value });
  };

  onReadOnlyChange = value => this.setState({ readOnly: value });

  onSchemaChange = e => {
    this.setState({ schemaName: e.target.value });
  };

  onLabelWidthChange = value => {
    this.setState({ labelWidth: value });
  };

  render() {
    const { showDescIcon, readOnly, labelWidth } = this.state;
    return (
      <div className='fr-playground'>
        <div className='w-100 flex setting-container'>
          <Radio.Group
            name='schemaName'
            defaultValue='simplest'
            className='w-50 flex items-center flex-wrap z-999'
            onChange={this.onSchemaChange}
          >
            <Radio value='simplest'>标准样例</Radio>
            <Radio value='basic'>基础控件</Radio>
            <Radio value='format'>校验</Radio>
            <Radio value='new-feature'>新功能</Radio>
            <Radio value='function'>复杂联动</Radio>
            <Radio value='input'>个性输入框</Radio>
            <Radio value='select'>个性选择框</Radio>
            <Radio value='date'>日期</Radio>
          </Radio.Group>
          <div className='w-50 flex items-center flex-wrap z-999'>
            <Select
              style={{ marginRight: 8 }}
              onChange={this.onColumnNumberChange}
              defaultValue='1'
            >
              <Option value='1'>一行一列</Option>
              <Option value='2'>一行二列</Option>
              <Option value='3'>一行三列</Option>
            </Select>
            <Select
              style={{ marginRight: 8 }}
              onChange={this.onDisplayChange}
              defaultValue='row'
            >
              <Option value='row'>左右排列</Option>
              <Option value='column'>上下排列</Option>
            </Select>
            <Switch
              style={{ marginRight: 8 }}
              checkedChildren='开描述'
              onChange={this.onShowDescChange}
              unCheckedChildren='关描述'
              checked={showDescIcon}
            />
            <Switch
              style={{ marginRight: 8 }}
              checkedChildren='编辑'
              onChange={this.onReadOnlyChange}
              unCheckedChildren='只读'
              checked={readOnly}
            />
            <div style={{ width: 42 }}>标签：</div>
            <Slider
              style={{ width: 80 }}
              max={200}
              min={20}
              value={labelWidth}
              onChange={this.onLabelWidthChange}
            />
          </div>
        </div>
        <Demo {...this.state} />
      </div>
    );
  }
}

export default Root;
